function ajaxLoadFundFeatureAndRisk(){
  var deferred = $.Deferred();
  var postdata = {};
  postdata.fundId = filterXSS(sessionStorage.getItem("fundid"));
	$.ajax({
		url: Constants.getSingleFundInfoUrl(),
      	type:"GET",
      	data:postdata
  	}).done(function(response){

  		var FundInfo = response;
  		$("#shortname").html(FundInfo.FundShortName);
      $("[data-role='moneytype']").loadMoneyType(FundInfo.MoneyDividendTypes);

      $(".js-with-header-content").css({
        "position":"absolute",
        "top":$("[data-role='header']").outerHeight(true) + "px"
      });
      
  		$("#fund-feature").html(FundInfo.FundFeature);
  		$("#fund-riskgrade").html("<img src='../img/risk-" + FundInfo.RiskGrade.toLowerCase() + ".jpg'>");
      deferred.resolve();
	});
  return deferred.promise();	
}

$(document).on( "pageinit", "#page-fund-fnr", function(envent) {

  App.StartLoading();

  $(".numura-warning").loadWarning();

  $.when(ajaxLoadFundFeatureAndRisk()).done(function(){
    App.StopLoading();
  });

});